<template>
  <span>
    <div id="title" class="text-h2 text-center ma-5 py-5">
      {{ title }}
    </div>
    <v-container fluid class="home pb-10 px-6">
      <v-row justify="center">
        <slot name="main-content">
          <v-col cols="12" md="8">
            <SlideShow :slides="slides" />
          </v-col>
        </slot>
      </v-row> 
      <v-row justify="center" class="mt-5">
        <v-col cols="12" md="8" xl="4">
          <v-card class="pa-5">
            <v-card-title>
              Introducing: {{ title }}
            </v-card-title>
            <v-card-text class="text-body-1">
              <slot name="description">
                {{ description }}
              </slot>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" md="8" xl="4">
          <v-card class="pa-5" >
            <v-card-title>
              Goals/Guidelines
            </v-card-title>
            <v-card-text class="text-body-1">
              <ul>
                <li 
                  v-for="(goal, i) in goals"
                  :key="`goal-${i}`"
                >{{ goal }}</li>
              </ul>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" md="8" xl="4">
          <v-card class="pa-5">
            <v-card-title>
              Challenges
            </v-card-title>
            <v-card-text class="text-body-1">
              <ul>
                <li
                  v-for="(challenge, i) in challenges"
                  :key="`challenge-${i}`"
                >{{ challenge }}</li>
              </ul>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center" v-if="this.$slots.additional">
        <v-col cols="12" md="8" xl="4">
          <v-card class="pa-5 mt-4">
            <v-card-title>
              Additional Information
            </v-card-title>
            <v-card-text class="text-body-1">
              <slot name="additional"></slot>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </span>
</template>
<style scoped>
  .v-card {
    height: 100%;
    word-break: break-word;
  }
  .v-card__title {
    word-break: break-word;
  }
</style>
<script>
import SlideShow from '@/components/SlideShow.vue'

export default {
  name: 'DetailsPage',
  components: {
    SlideShow
  },
  props: ['title', 'description', 'slides', 'goals', 'challenges']
}
</script>